@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap");

.home > div:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
}
.home > div {
  display: flex;
  flex-direction: column;
}
.homeHeading {
  margin-bottom: -250px;
}

.homeLeft {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-left: 50px;
  border-left: 20px solid royalblue;
}
.homeLeft > h1 {
  /* font-size: 95px; */
  border-bottom: 10px solid rgb(21, 33, 71);
}

.homeRight > img {
  border-radius: 100px;
  border-bottom: 20px solid royalblue;
}
.contactOnHome {
  width: 240px;
  margin-top: 30px;
  max-width: 240px;
  /* background-color: red; */
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contactOnHome button:hover{
  background-color: rgb(21, 33, 71);
  color: white;
}