/* @import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap"); */
.projectHeading {
  /* font-family: "Playfair Display", serif; */
  /* margin-bottom: -250px; */
  text-align: center;
  /* background-image: url('https://i.pinimg.com/originals/be/ce/0c/bece0c797cb134aefb2cb836578c9249.gif'); */
}
.projectContainer {
  margin: auto;
  border-left: 20px solid royalblue;
}
.projectContainer > H1 {
  margin: auto;
}
.pipe {
  font-size: 18px;
  font-weight: bold;
  background-color: #174650;
  padding: 2px 18px 3px 18px;
  border-radius: 10px;
  color: white;
}
.pipe1 {
  font-size: 16px;
  font-weight: bold;
  background-color: #2f855a;
  padding: 1px 20px 2px 20px;
  border-radius: 5px;
}

.Project1 > div > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.Project1 > div:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
}
.TechStack {
  margin-top: 20px;
}
.TechStack > div {
  border-radius: 10px;
  font-size: 17px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  padding: 10px;
}
.ProjectLeft {
  padding-left: 20px;
  text-align: left;
  padding-right: 20px;
}
.ProjectLeft > div:nth-child(1) {
  margin-top: 20px;
  /* background-color: royalblue; */
}
.ProjectLeft > h1 {
  /* margin-top: 20 px; */
  font-size: 35px;
  /* font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif; */
}
.ProjectLeft > h1::first-letter {
  font-size: 60px;
}

.ProjectLeft > p {
  margin-top: 5px;
  font-family: sans-serif;
}
.ProjectLeft > div:nth-child(5) {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ProjectLeft > div:nth-child(5) > img {
  width: 50px;
  height: 50px;
  border-radius: 50px;
}

.ProjectLeft > div:nth-child(5) > h1 {
  font-size: 16px;
  margin-left: 20px;
}
.ProjectRight > img {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  border: 2px solid white;
}
.githubButton {
  margin-top: 20px;
  display: flex;
}
.githubButton button {
  height: 30px;
  margin-left: 10px;
}

.ProjectRight > img {
  border-radius: 20px;
}
