@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap");

.footerBody {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
}
.footerBody > div:nth-child(1) {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.footerBody > div:nth-child(1) > h1 {
  font-size: 18px;
  font-family: "Playfair Display, serif";
}
.footerBody > div:nth-child(2) > div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footerBody > div:nth-child(2) > div > h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-family: "Playfair Display, serif";
}
