.techBody {
  /* background-image: url("https://freefrontend.com/assets/img/css-background-patterns/css-lattice-pattern.png"); */
  /* background-image: url('https://webartdevelopers.com/blog/wp-content/uploads/2020/06/css-particles-background.gif'); */
  /* background-color: url('https://cdn.dribbble.com/users/31818/screenshots/6835711/gif-dribble.gif'); */

  padding-bottom: 50px;
}
.techStackIcon {
  transition: 0.5s;
  transition-delay: 0.5s;
}

.techStackIcon > div {
  height: 250px;
  width: 180px;
  background-color: rgba(224, 224, 233, 0);
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  font-family: sans-serif;
  background: linear-gradient(
    3deg,
    rgb(236, 240, 243) 0%,
    rgba(175, 188, 255, 0) 70%
  );
  margin-top: 20px;
}
.techStackIcon :hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  color: black;
}
.techStackIcon > div > img {
  /* background-color: aqua; */
  height: 180px;
  width: 170px;
  border-radius: 20px;
}
.techStackIcon > div > h1 {
  background-color: royalblue;
  border: 1px solid white;
  color: white;
  font-size: 20px;
  padding: 4px 15px;
  border-radius: 15px;
}
