.container {
  border-radius: 10px;
  /* background-color: ; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.pipe1 {
  display: flex;
  text-align: left;
  gap: 20px;
}

.pipe {
  font-size: 18px;
  font-weight: bold;
  background-color: #174650;
  padding: 2px 18px 3px 18px;
  border-radius: 10px;
  color: white;
}

.TechStack {
  margin-top: 10px;
}

.TechStack > div {
  border-radius: 10px;
  font-size: 17px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.TitleDes {
  text-align: left;
}

.TitleDes > h1::first-letter {
  font-size: 60px;
}

.githubButton {
  margin-top: 20px;
  display: flex;
  border-radius: 20px;
}

.githubButton button {
  height: 30px;
  margin-right: 10px;
}
