.aboutContainer {
  border-right: 20px solid royalblue;
}

.aboutContainer > div {
  margin: auto;
}

.aboutHeading {
  /* font-family: 'Playfair Display', serif; */
  color: #f0f0f0;
  /* background-image: url('https://i.pinimg.com/originals/be/ce/0c/bece0c797cb134aefb2cb836578c9249.gif'); */
}

.aboutLeft {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.aboutLeft > img {
  border-radius: 20px;
}

.aboutRight {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  padding: 50px;
  /* border-right: 20px solid royalblue; */
  text-align: justify;
}

.aboutRight > p {
  margin-top: 20px;
  font-family: sans-serif;
  font-size: 18px;
  /* color: #1A365D; */
  font-weight: 500;
  line-height: 25px;
}

.aboutRight > p::first-letter {
  font-size: 40px;
  font-weight: bold;
  font-family: Georgia, "Times New Roman", Times, serif;
}
